.profile {
    width: 300px;
    background-color: #fff;
    color: #222;
  }
  
  .profile img {
    width: 300px;
    height: 40%;
  }
  
  .profile-content {
    padding: 10px;
  }
  
  h3 {
    font-size: 20px;
    line-height: 1.2;
    margin-bottom: 10px;
  }
  .text {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .text::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 100%;
    height: 0.5px;
    background: var(--color-orange);
  }
  
  .icons,
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
  }
  
  .icons > * {
    margin: 0 3px;
    cursor: pointer;
  }
  
  .btn a {
    font-size: 14px;
    font-weight: 400;
    padding: 7px 10px;
    border: 1px solid transparent;
    border-radius: 50px;
    outline: none;
    text-decoration: none;
    color: #fff;
    background: linear-gradient(
      135deg,
      rgba(203, 18, 231, 1) 44%,
      rgba(163, 1, 191, 1) 57%
    );
    cursor: pointer;
    transform: translateY(0);
    transition: all 0.3s;
  }
  
  .btn a:hover {
    transform: translateY(-5px);
  }
  